.cart-page{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
  gap: 3em;
  padding: 5em 7em;
  max-height: 100%;
  min-height: fit-content;
}

.cart-container{
  flex: 2;
}

.cart-pricing{
  flex: 1;
  align-self: flex-start;
  background-color: #fff;
  border: 1px solid #eee;
  border-radius: 1em;
  padding: 2em;
  box-shadow: 0 4px 10px rgba(0,0,0,0.05);
}

.cart-item{
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 2em;
  padding: 2em 0;
  border-bottom: 2px solid #f1f1f1;
}

.cart-item-image-container{
  flex-shrink: 0;
}

.cart-item-image{
  height: 7em;
  width: auto;
  border-radius: 0.5em;
}

.cart-item-info-container{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  row-gap: 0.25em;
  flex: 1;
}

.cart-item-title{
  font-size: 1em;
  font-weight: 500;
  line-height: 1.4em;
}

.small-gray{
  color: gray;
  font-size: 0.8em;
}

.cart-item-price{
  font-size: 1.3em;
  font-weight: bold;
}

.place-order-btn{
  width: 100%;
  padding: 0.9em 1.2em;
  color: #fff;
  border: none;
  border-radius: 0.5em;
  cursor: pointer;
  transition: 0.3s;
}

.cart-item-delete-btn {
  height: 100%;
  color: #ec2644;
  align-self: flex-end;
  cursor: pointer;
  font-size: 1.2rem;
  background: none;
  border: none;
  transition: transform 0.2s ease, color 0.2s ease;
}

.cart-item-delete-btn:hover {
  color: #d41e38;
  transform: scale(1.1);
}

/* Tablet view */
@media (max-width: 1024px) {
  .cart-item-delete-btn {
    font-size: 1rem;
    align-self: center;
    height: auto;
  }
}

/* Mobile view */
@media (max-width: 600px) {
  .cart-item-delete-btn {
    font-size: 0.9rem;
    align-self: center;
    margin-top: 0.5em;
  }
}



/* =======================
   RESPONSIVENESS
   ======================= */

/* Tablets */
@media (max-width: 992px) {
  .cart-page {
    flex-direction: column;
    padding: 3em 4em;
    gap: 2em;
  }

  .cart-pricing {
    width: 100%;
    order: 2;
  }

  .cart-container {
    order: 1;
    width: 100%;
  }

  .cart-item {
    gap: 1.5em;
    padding: 1.5em 0;
  }

  .cart-item-image {
    height: 6em;
  }

  .cart-item-title {
    font-size: 0.95em;
  }

  .cart-item-price {
    font-size: 1.1em;
  }
}

/* Mobile */
@media (max-width: 576px) {
  .cart-page {
    padding: 5em 1.5em;
    gap: 1.5em;
  }

  .cart-item {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1em;
    padding: 1em 0;
  }

  .cart-item-image {
    height: 5em;
  }

  .cart-item-info-container {
    align-items: center;
  }

  .cart-item-title {
    font-size: 0.9em;
  }

  .cart-item-price {
    font-size: 1em;
  }

  .cart-pricing {
    padding: 1.5em;
  }

  .add-to-cart-btn {
    font-size: 0.9em;
    padding: 0.8em 1em;
  }
}