.order-page{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 2em 7em;
  max-height: 100%;
  min-height: fit-content;
  flex-wrap: wrap;
}

.all-orders{
  width: 100%;
  display: flex;
  flex-direction: column-reverse;
}

.each-order{
  display: flex;
  flex-direction: column;
  gap: 2em;
  padding: 2em 0;
}

.order-container{
  display: flex;
  flex-direction: row;
  gap: 2em;
}

.order-item-info-container{
  display: flex;
  flex-direction: column;
  row-gap: 0.25em;
}

.order-item-image{
  height: 4em;
  width: auto;
}

.order-item-title{
  font-size: 0.8em;
}

.order-small-gray{
  color: gray;
  font-size: 0.6em;
}

.order-item-price{
  font-size: 0.8em;
}

.font-s-08{
  font-size: 0.8em;
}

.each-order-info-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.order-date-id{
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}


/* ✅ Tablet screens */
@media (max-width: 992px) {
  .order-page {
    padding: 2em 3em;
  }

  .order-container {
    flex-direction: row;
    align-items: flex-start;
  }

  .order-item-image {
    height: 5em;
  }

  .each-order-info-container {
    flex-direction: column;
    gap: 1.5em;
  }

  .order-date-id {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    gap: 0.5em 1em;
  }
}

/* ✅ Mobile screens */
@media (max-width: 600px) {
  .order-page {
    padding: 1.5em;
  }

  .each-order {
    gap: 1.5em;
  }

  .order-container {
    flex-direction: row;
    align-items: flex-start;
    gap: 2em;
  }

  .order-item-image {
    height: 3.5em;
  }

  .order-item-title {
    font-size: 0.85em;
  }

  .order-item-price,
  .order-item-quantity,
  .font-s-08 {
    font-size: 0.75em;
  }

  .each-order-info-container {
    flex-direction: column;
    gap: 1em;
  }

  .order-date-id {
    font-size: 0.75em;
  }
}