.login-page{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;

}

.login-container{
  padding: 1em 1em;
  border: solid 1px #e0e0e2;
  display: flex;
  flex-direction: column;
  row-gap: 1em;
  border-radius: 10px;
}

.login-header{
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  column-gap: 2em;
}

.login-signup-text,
.login-vertical-line,
.login-enter-text{
  color: #000;
}

.go-back-icon {
    width: 32px;
    height: 32px;
    cursor: pointer;
    transform: rotate(180deg);
    opacity: 0.7;
    transition: opacity 0.3s;
}

.go-back-icon:hover {
    opacity: 1;
}

.login-content {
    padding: 2em 1.25em;
    padding-bottom: 0;
}

.login-page-login-text {
    display: flex;
    align-items: center;
    margin-bottom: 25px;
}

.login-vertical-line {
    margin: 0 10px;
}

.login-signup-text {
    font-size: 18px;
}

.login-hi {
  color: #56595d;
    font-size: 12px;
    margin-bottom: 5px;
}

.login-enter-phone-text {
    color: #56595d;
    font-size: 12px;
    margin-bottom: 20px;
}

.login-form {
    margin-bottom: 20px;
}

.login-phone-input {
    width: 100%;
    padding: 0.8em 0;
    border: 1px solid #c0c2c5;
    border-radius: 8px;
    font-size: 16px;
    margin-bottom: 5px;
    text-align: center;
    direction: ltr;
}

.login-phone-input:focus {
    outline: none;
    border-color: #ef394e;
    box-shadow: 0 0 0 3px rgba(239, 57, 78, 0.1);
}

.empty-phone-warning {
    color: #ef394e;
    font-size: 12px;
    height: 20px;
    margin-bottom: 10px;
}

.login-submit-button {
    width: 100%;
    padding: 15px;
    background-color: #ef394e;
    color: white;
    border: none;
    border-radius: 8px;
    font-size: 16px;
    font-weight: 700;
    cursor: pointer;
    transition: background-color 0.3s;
}

.login-submit-button:hover {
    background-color: #e6122a;
}

.login-submit-button:active{
  opacity: 0.7;
}

.login-policies {
    color: #a1a3a6;
    font-size: 12px;
    text-align: center;
    line-height: 1.8;
}

@media (max-width: 480px) {
    .login-page {
        padding: 10px;
    }
    
    .login-content {
        padding: 20px 15px;
    }
}



/* Verification container styling - matches your theme */
.verification-container {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin: 20px 0;
  direction: ltr;
}

.verification-code {
  width: 50px;
  height: 60px;
  border: 1px solid #c0c2c5;
  border-radius: 8px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
  outline: none;
  transition: all 0.3s ease;
  background-color: #ffffff;
  color: #000;
  direction: ltr;
}

.verification-code:focus {
  outline: none;
  border-color: #ef394e;
  box-shadow: 0 0 0 3px rgba(239, 57, 78, 0.1);
}

.verification-code:hover {
  border-color: #999;
}

/* Remove number input arrows/spinners */
.verification-code::-webkit-outer-spin-button,
.verification-code::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.verification-code {
  -moz-appearance: textfield;
}

/* Error state */
.verification-code:invalid {
  border-color: #ef394e;
  background-color: #fff5f5;
}

/* Success state when filled */
.verification-code:valid {
  border-color: #28a745;
  background-color: #f8fff9;
}

/* Responsive design */
@media (max-width: 480px) {
  .verification-container {
    gap: 8px;
  }
  
  .verification-code {
    width: 45px;
    height: 55px;
    font-size: 18px;
  }
}

@media (max-width: 360px) {
  .verification-container {
    gap: 6px;
  }
  
  .verification-code {
    width: 40px;
    height: 50px;
    font-size: 16px;
  }
}

/* ✅ Responsive Design */
@media (max-width: 768px) {
  .login-container {
    padding: 1em;
    max-width: 90%;
  }

  .login-page-login-text {
    font-size: 1rem;
  }

  .verification-code {
    width: 45px;
    height: 55px;
    font-size: 1.1rem;
  }
}

@media (max-width: 480px) {
  .login-container {
    box-shadow: none;
    padding: 0.8em;
  }

  .login-content {
    padding: 1em 0;
  }

  .login-header img {
    width: 24px;
    height: 24px;
  }

  .verification-container {
    gap: 8px;
  }

  .verification-code {
    width: 42px;
    height: 52px;
    font-size: 1rem;
  }

  .login-phone-input {
    font-size: 0.95rem;
  }

  .login-submit-button {
    padding: 0.8em;
    font-size: 0.95rem;
  }

  .login-enter-phone-text,
  .login-policies {
    font-size: 0.75rem;
  }
}

@media (max-width: 360px) {
  .verification-container {
    gap: 6px;
  }

  .verification-code {
    width: 38px;
    height: 48px;
    font-size: 0.9rem;
  }

  .login-phone-input {
    font-size: 0.9rem;
    padding: 0.7em;
  }
}