/* ===============================
   🏠 LANDING PAGE CONTAINER
   =============================== */

.landing-page {
  max-width: 100%;
  height: auto;
  padding: 2em 1em;
  overflow-x: hidden;
  position: relative;
  flex: 0 0 auto;
}

/* ===============================
   🎠 LANDING CAROUSEL
   =============================== */

.landing-carousel-container {
  position: relative;
  width: 100%;
  margin: 0 0 4rem 0;
  overflow: hidden;
  border-radius: 1em;
}

.landing-carousel-scroll {
  direction: ltr;
  overflow: hidden;
  width: 100%;
}

.landing-carousel {
  display: flex;
  flex-direction: row;
  scroll-behavior: smooth;
  width: 100%;
}

.landing-slide {
  height: 20em;
  min-width: 100%;
  max-width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 1em;
  cursor: pointer;
  position: relative;
  overflow: hidden;
  margin: 0;
  transition: all 0.3s ease-in-out;
}

/* Themed backgrounds */
.landing-slide.men {
  background-image: url('../../Images/LandingPage/Rectangle\ 1.png');
}

.landing-slide.women {
  background-image: url('../../Images/LandingPage/Rectangle\ 2.png');
}

.landing-slide.tech {
  background-image: url('../../Images/LandingPage/Rectangle\ 3.png');
}

/* Carousel images */
.landing-image,
.landing-clothes {
  width: 18%;
  transition: transform 0.3s ease-in-out;
}

.landing-slide:hover .landing-image,
.landing-slide:hover .landing-clothes {
  transform: scale(1.05);
}

/* Carousel navigation buttons */
.landing-carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: transparent;
  border: none;
  cursor: pointer;
  padding: 0;
  z-index: 2;
  opacity: 0;
}

.opacity-one {
  opacity: 1;
}

.landing-carousel-btn.left {
  left: 0.5rem;
}

.landing-carousel-btn.right {
  right: 0.5rem;
}

.landing-carousel-btn img {
  width: 2em;
  height: 2em;
  opacity: 0.8;
  transition: opacity 0.2s;
}

.landing-carousel-btn img:hover {
  opacity: 1;
}

/* Dots below the carousel */
.landing-dots {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 1rem;
  width: 100%;
  gap: 0.5rem;
}

.dot {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background: #ccc;
  cursor: pointer;
  transition: background-color 0.3s;
}

.dot.active {
  background-color: #333;
}

/* ===============================
   🛍️ POPULAR / MOST SOLD SECTIONS
   =============================== */

.most-popular-container {
  margin: 2em 1em 5em 1em;
  height: fit-content;
}

.most-popular-header {
  display: flex;
  flex-direction: row;
  align-items: end;
  column-gap: 0.5em;
  margin: 3em 0;
}

.most-popular-header-text {
  width: fit-content;
  height: fit-content;
}

.horizontal-line {
  height: 1px;
  background-color: #e2e4eb;
}

.most-popular-line {
  flex: 1;
}

/* Popular products container */
.popular-products {
  display: flex;
  flex-direction: row;
  direction: ltr;
  gap: 0.75em;
  overflow-x: hidden;
  padding: 1em;
  scroll-snap-type: x mandatory;
  -webkit-overflow-scrolling: touch;
  background: linear-gradient(135deg, #81774d, #a09163);
  border-radius: 0.5em;
  scroll-behavior: smooth;
  width: 95%;
}

/* Hide scrollbar */
.popular-products::-webkit-scrollbar {
  display: none;
}

.popular-products {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

/* Product cards */
.popular-product-card {
  flex: 0 0 auto;
  width: 13.05em;
  height: 20.55em;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  row-gap: 1em;
  background-color: #fff;
  border-radius: 0.5em;
  box-sizing: border-box;
  cursor: pointer;
  padding: 0.5em;
  scroll-snap-align: start;
  transition: box-shadow 0.2s ease;
}

.popular-product-card:hover {
  box-shadow:
    0 6px 12px rgba(0, 0, 0, 0.1),
    0 12px 24px rgba(0, 0, 0, 0.08),
    0 18px 36px rgba(0, 0, 0, 0.05);
  z-index: 1;
}

.popular-product-image {
  height: 60%;
  max-width: 100%;
  object-fit: contain;
  padding: 0.5em 0;
}

.popular-product-title {
  font-size: 0.75em;
  color: #3f4064;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: center;
}

.popular-price {
  margin-top: auto;
  font-weight: bold;
  font-size: 0.8em;
  color: inherit;
  margin-right: auto;
}

.arrow-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.arrow-container p {
  color: #3f4064;
  font-size: 0.8em;
  font-weight: 600;
}

/* Scroll buttons for product sliders */
.popular-products-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  overflow: visible;
}

.popular-scroll-button {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 2.5em;
  height: 2.5em;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  z-index: 2;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  transition: background-color 0.2s;
}

.popular-scroll-button:hover {
  background-color: rgba(255, 255, 255, 1);
}

.popular-scroll-button.left {
  left: 0.5em;
}

.popular-scroll-button.right {
  right: 0.5em;
}

.popular-scroll-button img {
  width: 1em;
  height: 1em;
}

/* ===============================
   📱 RESPONSIVE DESIGN
   =============================== */

/* --- Tablets --- */
@media (max-width: 1024px) {
  .landing-carousel-container {
    width: 95%;
  }

  .landing-slide {
    height: 18em;
  }

  .landing-image,
  .landing-clothes {
    width: 25%;
  }

  .popular-product-card {
    width: 11em;
    height: 18em;
  }
}

/* --- Mobile Devices --- */
@media (max-width: 768px) {
  .landing-page {
    padding: 1em 0.5em;
  }

  .landing-carousel-container {
    width: 100%;
    margin-bottom: 2rem;
  }

  .landing-slide {
    flex-direction: row;
    flex: 0 0 100%;
    min-width: 100%;
    max-width: 100%;
    height: 12em;
    scroll-snap-align: start;
    border-radius: 80px;
    margin: 0 0 2em 0;
    overflow-x: scroll;
    corner-shape: squircle;
  }

  .landing-image,
  .landing-clothes {
    min-width: 30%;
    max-width: auto;
    min-height: auto;
    max-height: 100%;
    margin: 0.5em auto;
  }

  .landing-carousel-btn {
    display: none;
  }

  .landing-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5em;
    position: relative;
    margin-top: 1em;
  }

  .landing-carousel-scroll {
    display: flex;
    overflow-x: auto;
    overflow-y: hidden;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    -webkit-overflow-scrolling: touch;
    width: 100%;
  }

  .landing-carousel-scroll::-webkit-scrollbar {
    display: none;
  }

  .popular-scroll-button {
    display: none;
  }

  .popular-product-card {
    width: 9em;
    height: 16em;
    row-gap: 0.5em;
  }

  .popular-product-title {
    font-size: 0.7em;
  }

  .popular-price {
    font-size: 0.75em;
  }
}

/* --- Small Mobile --- */
@media (max-width: 480px) {
  .landing-page {
    padding: 6em 0.5em;
  }

  .landing-image,
  .landing-clothes {
    width: 35%;
  }

  .popular-product-card {
    width: 8em;
    height: 14em;
  }

  .popular-product-image {
    height: 50%;
  }

  .popular-product-title {
    font-size: 0.65em;
  }

  .popular-price {
    font-size: 0.7em;
  }

  .arrow-container p {
    font-size: 0.7em;
  }

  .dot {
    width: 8px;
    height: 8px;
  }
}

/* --- Large Screens --- */
@media (min-width: 1440px) {
  .landing-carousel-container {
    width: 80%;
  }

  .landing-image,
  .landing-clothes {
    width: 17%;
  }

  .landing-slide {
    height: 22em;
  }

  .popular-product-card {
    width: 14em;
    height: 22em;
  }
}

/* --- Desktop (min-width 1025px) --- */
@media (min-width: 1025px) {
  .landing-carousel-container {
    width: 100%;
  }
}




.category-card-container {
  display: flex;
  justify-content: center;
  align-items: stretch;
  gap: 1rem;
  flex-wrap: nowrap;
  width: 90%;
  max-width: 1100px;
  margin: 2rem auto;
}

.category-card {
  flex: 1 1 180px;
  /* smaller minimum width */
  max-width: 200px;
  /* limit max size */
  background: #fff;
  border-radius: 0.75rem;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  text-align: center;
  transition: all 0.25s ease;
  cursor: pointer;
  text-decoration: none;
  color: inherit;
}

.category-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 15px rgba(0, 0, 0, 0.12);
}

.category-card-img {
  width: 100%;
  height: 140px;
  /* smaller height */
  object-fit: contain;
  /* ✅ ensures full image is visible */
  background-color: #f8f8f8;
  /* helps if image has transparency */
  padding: 0.5rem;
  display: block;
}

.category-card-title {
  font-size: 1rem;
  font-weight: 600;
  padding: 0.75rem 0.5rem;
  background: #f9f9f9;
  color: #333;
}


/* 📱 Mobile and tablet screens */
@media (max-width: 1024px) {
  .category-card {
    max-width: 8em;
    max-height: 9.5em;
  }

  .category-card-img {
    max-height: 6.5em;
  }
}

@media (max-width: 768px) {
  .category-card-container {
    flex-direction: row;
    flex-wrap: nowrap;
  }

  .category-card {
    max-width: 7em;
    max-height: 8.5em;
  }

  .category-card-img {
    height: 6em;
  }

  .category-card-title {
    font-size: 0.85rem;
    padding: 0.5rem 0;
  }
}

/* 📱 Extra small (phones under 480px) */
@media (max-width: 480px) {
  .category-card-container {
    flex-direction: row;
    flex-wrap: nowrap;
  }

  .category-card {
    max-width: 4em;
    max-height: 5em;
  }

  .category-card-img {
    height: 3.5em;
  }

  .category-card-title {
    font-size: 0.6rem;
    padding-top: 0;
  }
}


.landing-bottom-img-container {
  display: flex;
  justify-content: space-evenly;
}

.landing-bottom-img {
  border-radius: 3em;
}

.ad-container {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(to right, #927a5e, #2c251c);
  border-radius: 16px;
  padding: 20px 30px;
  width: 31.25em;
  height: 15em;
  color: #f5e7d1;
  direction: rtl;
  font-family: "Tahoma", sans-serif;
}

.ad-image img {
  height: 15.2em;
  object-fit: contain;
}

.ad-content {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
}

.ad-content h2 {
  font-size: 22px;
  margin: 0;
}

.ad-options {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.option {
  display: flex;
  align-items: center;
  gap: 8px;
}

.dot {
  width: 10px;
  height: 10px;
  background-color: #cbb79a;
  border-radius: 50%;
}

.order-btn {
  background-color: #cbb79a;
  color: #3c2b1c;
  border: none;
  border-radius: 25px;
  padding: 8px 18px;
  font-weight: bold;
  cursor: pointer;
  transition: background-color 0.3s;
}

.order-btn:hover {
  background-color: #d8c3a3;
}


/* ===============================
   📱 Responsive - Bottom Ads
   =============================== */

/* --- Tablets --- */
@media (max-width: 1024px) {
  .landing-bottom-img-container {
    flex-direction: row;
    align-items: center;
    gap: 2rem;
  }

  .ad-container {
    width: 90%;
    height: auto;
    flex-direction: row-reverse;
    padding: 16px 20px;
  }

  .ad-image img {
    height: 12em;
  }

  .ad-content h2 {
    font-size: 20px;
  }
}

/* --- Mobile Devices --- */
@media (max-width: 768px) {
  .landing-bottom-img-container {
    flex-direction: row;
    align-items: center;
    gap: 1.5rem;
  }

  .ad-container {
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 90%;
    height: auto;
    padding: 1.5rem;
    border-radius: 65px / 50px;
  }

  .ad-image img {
    height: 10em;
  }

  .ad-content {
    align-items: center;
    gap: 12px;
  }

  .ad-content h2 {
    font-size: 18px;
  }

  .ad-options {
    align-items: center;
  }

  .order-btn {
    padding: 8px 16px;
    font-size: 0.9rem;
  }
}

/* --- Small Mobile (under 480px) --- */
@media (max-width: 480px) {
  .landing-bottom-img-container {
    gap: 1rem;
  }

  .ad-container {
    width: 95%;
    padding: 1rem;
    border-radius: 40px / 45px;
  }

  .ad-image img {
    height: 6em;
  }

  .ad-content h2 {
    font-size: 16px;
  }

  .ad-options {
    font-size: 0.7rem;
  }

  .order-btn {
    font-size: 0.7rem;
    padding: 6px 12px;
  }
}