@import url('https://fonts.googleapis.com/css2?family=Lobster&display=swap');



.header {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 100%;
  border: none;
  border-bottom: solid 1px #00000012;
  margin-bottom: 1px;

  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 900;

  background-color: #ffffff;
  transition: all 0.2s ease-in-out;
  box-shadow: 0px 6px 7px 0px rgba(135, 145, 172, 0.07);
}

.header-ad-image {
  width: 100%;
  margin-bottom: 0.5em;
}

/* search */

.header-search {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 100%;
  padding: 1em;
  padding-bottom: 0.25em;

  position: sticky;
  top: 0;
  left: 0;
  right: 0;
  z-index: 900;
}

.header-search-right {
  display: flex;
  flex-direction: row;
  column-gap: 1em;
  width: 80%;
}

.shop-logo-container {
  width: 15%;
  align-self: center;
  align-content: center;
}

.shop-logo {
  width: 100%;
  color: #ec2644;
  font-size: 2em;
  font-family: "Lobster", sans-serif;
}

.search-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  text-align: center;
  width: 60%;
  height: 3em;
  background-color: #00000014;
  border-radius: 5px;
  padding: 0 0.5em;
}

.search-label {
  height: 85%;
  width: 3em;
  background-color: transparent;
  border-radius: 5px;
  z-index: 1;
  position: relative;
  right: 0.5em;
  cursor: pointer;
}

.search-input {
  height: 80%;
  width: 100%;

  border: none;
  border-radius: 5px;
  background-color: transparent;
  z-index: 1;
  font-size: 1em;
}

.search-input:focus {
  outline: none;
  border: none;
  border-bottom: solid 1px #2586ff;
}

.header-menu-left {
  display: flex;
  flex-direction: row;
  justify-content: end;

  column-gap: 0.6em;
  width: 20%;
  padding: 0 0.5em;
}

.notif-link,
.login-link,
.cart-link,
.search-label {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.login-section {
  font-size: 0.85em;
  column-gap: 0.8em;

  border: solid 1px #0000002b;
  border-radius: 5px;

  width: fit-content;
  height: 3em;
  padding: 0 0.75em;
}

.login-text {
  display: flex;
  flex-direction: row;
  align-items: center;
  column-gap: 0.4em;
}

.cart-link {
  width: fit-content;
  border: none;
  margin-top: -0.25em;
}

.cart-vertical-line {
  color: #0000002b;
  font-size: 1.5em;
  padding: 0;
  margin-left: 1em;
}

.cart-icon {
  padding-right: 0;
}

.notif-icon,
.login-icon,
.cart-icon {
  height: 1.5em;
  width: 1.5em;
}

.search-icon {
  height: 1.25em;
  width: 1.25em;
}

/* hamburger menu */
.menu-container {
  display: flex;
  flex-direction: row;
  column-gap: 0.5em;
  align-items: center;
  height: 3em;
}

.menu-icon {
  height: 1.25em;
  width: 1.25em;
}

.menu-text {
  display: flex;
  flex-direction: row;
  column-gap: 0.5em;
  padding: 0 0;
  padding-bottom: 0.25em;
  font-size: 0.95em;
}

.menu-wrapper {
  position: relative;
  display: flex;
  flex-direction: row;
  width: fit-content;
  cursor: pointer;
  transition: all 0.6s ease-in-out;
  z-index: 1000;
}

.menu-container::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  /* thickness of the border */
  background-color: #ec2644;

  transform: scaleX(0);
  /* start hidden */
  transform-origin: left;
  /* animation starts from left */
  transition: transform 0.3s ease;
}

.menu-container:hover::after {
  transform: scaleX(1);
  /* grows to full width */
}

.menu-content {
  position: absolute;
  top: 101%;
  /* appears below the menu icon */
  right: 0;
  /* align with right edge */
  background: #ffffff;
  border: 1px solid #0000002b;
  border-top: none;
  padding: 0 0.6em;
  z-index: 1000;
  width: 200%;
}

.menu-content a {
  display: block;
  /* makes them act like your <p> */
  max-width: 100%;
  height: 100%;
  margin: 0;
  padding: 0.5em 0.7em;
  text-decoration: none;
  /* remove underline */
  color: inherit;
}

.menu-content a:hover {
  border-bottom: solid 1px #0000002b;
  border-top: solid 1px #0000002b;
}

.header-bottom-section {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1em;
  padding-bottom: 0;
}

/* پشتیبانی */

.header-search-left {
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-start;

  column-gap: 1em;
  width: 20%;
  padding: 0 0.5em;
}

.phone-icon-container {
  display: flex;
  align-items: center;
  align-self: center;
  justify-content: center;

  width: 2.5em;
  height: 2.5em;
  background-color: #ec2644;
  border-radius: 0.25em;
}

.phone-icon {
  height: 1.5em;
  width: 1.5em;
}

.phone-text-container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  text-align: end;
  padding: 0.5em 0;
}

.phone-number {
  font-size: 0.75em;
  color: #ec2644;
}

.phone-text {
  font-size: 0.6em;
}


.website-links {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  column-gap: 1em;
  align-items: center;
  width: 60%;
}

.home-page-text,
.shop-page-text,
.about-page-text,
.contact-page-text {
  cursor: pointer;
  transition: all 0.6s ease-in-out;
  position: relative;
  height: 100%;
  align-content: center;

  padding-bottom: 0.25em;
  font-size: 0.95em;
}

.home-page-text::after,
.shop-page-text::after,
.about-page-text::after,
.contact-page-text::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 1px;
  /* thickness of the border */
  background-color: red;

  transform: scaleX(0);
  /* start hidden */
  transform-origin: left;
  /* animation starts from left */
  transition: transform 0.3s ease;
}

.home-page-text:hover::after,
.shop-page-text:hover::after,
.about-page-text:hover::after,
.contact-page-text:hover::after {
  transform: scaleX(1);
  /* grows to full width */
}



/* hide and show when scroll up and down */
.header-bottom-section {
  transition: opacity 0.3s ease, visibility 0.3s ease, transform 0.3s ease;
}

.header-bottom-section.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  /* stay in place */
}

.header-bottom-section.hide {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-20px);
  /* slide up slightly */
}

.header-ad {
  transition: opacity 0.3s ease, visibility 0.3s ease, transform 0.3s ease;
}

.header-ad.show {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
  /* stay in place */
}

.header-ad.hide {
  opacity: 0;
  visibility: hidden;
  transform: translateY(-20px);
  /* slide up slightly */
}


.display-none {
  display: none;
}


/* ===============================
   📱 RESPONSIVE DESIGN
   =============================== */

/* --- Tablets --- */
@media (max-width: 1024px) {
  .header-search {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75em;
  }

  .header-search-right {
    width: 100%;
  }

  .header-search-left {
    width: 100%;
    justify-content: flex-end;
  }

  .search-container {
    width: 100%;
  }

  .website-links {
    gap: 0.75em;
  }
}

/* --- Mobile (like DigiKala) --- */
@media (max-width: 768px) {
  .header {
    border-bottom: 1px solid #f0f0f0;
  }

  .header-search {
    flex-direction: row-reverse;
    justify-content: space-between;
    padding: 0.75em;
  }

  .shop-logo {
    font-size: 1.6em;
  }

  .header-search-left {
    display: none;
  }

  .search-container {
    display: none;
  }

  .search-label {
    display: flex;
  }

  /* show compact icons instead */
  .header-menu-left {
    display: flex;
    gap: 1em;
  }

  .login-section {
    border: none;
    padding: 0;
  }

  .menu-wrapper {
    display: flex;
  }

  .header-bottom-section {
    justify-content: space-around;
    font-size: 0.9em;
    padding: 0.5em 0.25em;
  }

  .website-links {
    display: none;
  }

  /* Floating mobile search (like DigiKala) */
  .mobile-search {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background: #fff;
    z-index: 9999;
    padding: 1em;
    display: flex;
    align-items: center;
    gap: 0.5em;
    border-bottom: 1px solid #ddd;
  }

  .mobile-search input {
    flex: 1;
    padding: 0.5em;
    border: 1px solid #ccc;
    border-radius: 0.25em;
    font-size: 1em;
  }

  .mobile-search button {
    background: none;
    border: none;
    cursor: pointer;
  }
}

/* --- Small Mobile --- */
@media (max-width: 480px) {
  .shop-logo {
    font-size: 1.4em;
  }

  .menu-icon,
  .cart-icon,
  .login-icon,
  .search-icon {
    width: 1.2em;
    height: 1.2em;
  }
}


/* ---------- RESPONSIVE HEADER ---------- */

/* --- Tablet (<= 1024px) --- */
@media screen and (max-width: 1024px) {
  .header-search {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75em;
  }

  .header-search-right {
    width: 100%;
    justify-content: space-between;
  }

  .shop-logo-container {
    width: auto;
  }

  .search-container {
    width: 100%;
  }

  .header-search-left {
    width: 100%;
    justify-content: space-between;
  }

  .website-links {
    column-gap: 0.75em;
  }

  .login-section {
    font-size: 0.8em;
  }
}

/* --- Mobile (<= 768px) --- */
@media screen and (max-width: 768px) {
  .header {
    position: fixed;
    top: 0;
    width: 100%;
  }

  /* Simplify layout */
  .header-search {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0.5em 1em;
  }

  .header-search-right {
    width: auto;
  }

  .shop-logo {
    font-size: 1.6em;
  }

  .search-container {
    display: none;
    /* hidden until toggled by icon */
  }

  .search-icon {
    width: 1.5em;
    height: 1.5em;
  }

  .header-search-left {
    display: none;
    /* hide support info */
  }

  /* Bottom section becomes floating menu */
  .header-bottom-section {
    display: none;
    /* hidden by default */
  }

  /* Hamburger icon for mobile */
  .menu-wrapper {
    display: flex;
    align-items: center;
  }

  .menu-icon {
    width: 1.75em;
    height: 1.75em;
  }

  /* Add a fixed bottom navigation bar like Digikala */
  .mobile-nav {
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
    background: #fff;
    border-top: 1px solid #ddd;
    padding: 0.5em 0;
    z-index: 999;
  }

  .mobile-nav a,
  .mobile-search-label,
  .mobile-menu-icon-container {
    text-decoration: none;
    color: #333;
    font-size: 0.8em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .mobile-nav img,
  .mobile-search-label img,
  .mobile-menu-icon-container img {
    width: 1.5em;
    height: 1.5em;
    margin-bottom: 0.2em;
  }
}

/* --- Very Small (<= 480px) --- */
@media screen and (max-width: 480px) {
  .shop-logo {
    font-size: 1.3em;
  }

  .login-section {
    height: 2.5em;
    font-size: 0.75em;
  }

  .menu-icon {
    width: 1.5em;
    height: 1.5em;
  }

  .mobile-nav img,
  .mobile-search-label img,
  .mobile-menu-icon-container img {
    width: 1.3em;
    height: 1.3em;
  }

  .mobile-nav a,
  .mobile-search-label,
  .mobile-menu-icon-container {
    font-size: 0.7em;
  }
}


/* ---------- Collapsible Mobile Search ---------- */

.mobile-search-toggle {
  display: none;
  background: none;
  border: none;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .search-container {
    display: none;
  }

  .mobile-search-toggle {
    display: flex;
    align-items: center;
    justify-content: center;
    background: none;
    border: none;
    padding: 0.25em;
  }

  /* Active state (expanded search) */
  .search-container.mobile-active {
    display: flex !important;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 3.5em;
    background: #fff;
    border-bottom: 1px solid #ddd;
    padding: 0 1em;
    z-index: 1001;
    animation: expandSearch 0.3s ease;
  }

  @keyframes expandSearch {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }

    to {
      opacity: 1;
      transform: translateY(0);
    }
  }

  .search-container.mobile-active .search-input {
    width: 100%;
    border: none;
    font-size: 1em;
  }

  .search-container.mobile-active .search-input:focus {
    outline: none;
    border: none;
    border-bottom: solid 1px #2586ff;
  }

  .header-search-left {
    display: none;
  }

  /* Hide other elements when search is open */
  .header-bottom-section,
  .menu-wrapper,
  .header-menu-left {
    display: none !important;
  }
}

/* ========== Mobile Side Menu ========== */

.mobile-menu-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
  z-index: 998;
}

.mobile-menu-overlay.active {
  opacity: 1;
  visibility: visible;
}

.mobile-side-menu {
  position: fixed;
  top: 0;
  right: -80%;
  width: 80%;
  max-width: 320px;
  height: 100vh;
  background-color: #fff;
  box-shadow: -4px 0 12px rgba(0, 0, 0, 0.15);
  z-index: 999;
  transition: right 0.3s ease;
  display: flex;
  flex-direction: column;
}

.mobile-side-menu.active {
  right: 0;
}

.mobile-menu-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1em;
  border-bottom: 1px solid #eee;
}

.mobile-menu-title {
  font-weight: bold;
  color: #333;
  font-size: 1.1em;
}

.mobile-menu-close {
  background: none;
  border: none;
  font-size: 1.3em;
  cursor: pointer;
  color: #ec2644;
}

.mobile-menu-links {
  display: flex;
  flex-direction: column;
  padding: 1em;
  gap: 1em;
}

.mobile-menu-links a {
  text-decoration: none;
  color: #333;
  font-size: 1em;
  transition: color 0.2s;
}

.mobile-menu-links a:hover {
  color: #ec2644;
}

/* Show only on mobile */
@media screen and (min-width: 769px) {

  .mobile-side-menu,
  .mobile-menu-overlay {
    display: none;
  }
}