.footer{
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  max-width: 100%;
  height: 50vh;
  padding: 0 5em;
}

.for-customers{
  display: flex;
  flex-direction: row;
  flex: 0 0 40%;
}

.for-customers a{
  transition: all 0.4s ease;
}

.for-customers a:hover{
  color: #ec2644;
}

.guide-container{
  display: flex;
  flex-direction: column;
  min-width: 0;
  flex: 0 1 auto;
  width: 50%;
}

.guide-text{
  display: flex;
  flex-direction: column;
}

.guide-text a{
  padding: 1em 0;
}

.categories-container{
  min-width: 0;
  width: 50%;
  flex: 0 1 auto;
}

.categories-text{
  display: flex;
  flex-direction: column;
}

.categories-text a{
  padding: 1em 0;
}

.full-width{
  width: 100%;
}

.footer h3{
  color: #969fbd;
  padding: 1em 0.5em;
}

.social-icon-container{
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}

.instagram-link,
.whatsapp-link,
.telegram-link{
  height: 1.3em;
  padding: 0 1em;

  display: flex;
  flex-direction: row;
  column-gap: 0.2em;
  transition: opacity 0.4s ease, transform 0.4s ease;
}

.instagram-icon,
.whatsapp-icon,
.telegram-icon{
  height: 1.3em;
  transition: opacity 0.4s ease;
}

.half-opacity{
  opacity: 0.5;
}

.instagram-text,
.whatsapp-text,
.telegram-text{
  font-size: 0.8em;
  color: #046b6b;
  align-self: center;
  opacity: 0;             /* hidden */
  visibility: hidden;     /* not focusable */
  transform: translateX(-5px); /* small slide-in effect */
  transition: opacity 0.4s ease, transform 0.4s ease;
}

.social-text-visible{
  opacity: 1;
  visibility: visible;
  transform: translateX(0);
}



.footer2-upper-section{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 3em;
  margin-bottom: 4em;
  align-items: center;
}

.bigger-logo-container{
  width: fit-content;
}
.bigger-logo{
  font-size: 4em;
}

.footer2-right-section{
  display: flex;
  flex-direction: column;
}

.footer2-right-section p,
.footer2-right-section h4{
  padding: 0.5em 0;
}

.footer2-right-section p{
  color: #6e789f;
  font-size: 0.7em;
}

.footer2-left-section{
  display: flex;
  flex-direction: column;
  row-gap: 0.5em;
}

.phone-container,
.gmail-container,
.location-container{
  display: flex;
  flex-direction: row-reverse;
  column-gap: 0.5em;
  text-align: center;
  align-items: center;
}

.phone-icon,
.gmail-icon,
.location-icon{
  height: auto;
  width: 30px;
}

.footer2-lower-section{
  text-align: center;
  height: 7em;
  color: #4d5e8a;
  font-size: 11px;
  font-weight: 600;
  align-content: center;
}






/* -------------------------
   🔹 Mobile Styles (≤ 600px)
-------------------------- */
@media (max-width: 600px) {
  .footer {
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 2em 1.5em;
  }

  .for-customers {
    width: 100%;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  .guide-container,
  .categories-container {
    width: 100%;
  }

  .social-media-container {
    width: 100%;
    align-items: center;
  }

  .social-icon-container {
    justify-content: center;
  }

  .footer2-upper-section {
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 2em 1.5em;
  }

  .footer2-left-section {
    align-items: center;
  }

  .bigger-logo {
    font-size: 2em;
  }

  .footer2-right-section p {
    font-size: 0.9em;
  }
}

/* -------------------------
   🔸 Tablet Styles (600px–1024px)
-------------------------- */
@media (min-width: 601px) and (max-width: 1024px) {
  .footer {
    flex-direction: row-reverse;
    flex-wrap: wrap;
    padding: 2em 3em;
  }

  .for-customers {
    flex: 1 1 50%;
    justify-content: space-between;
  }

  .social-media-container {
    flex: 1 1 40%;
    align-items: flex-start;
  }

  .footer2-upper-section {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 2em 3em;
  }

  .footer2-left-section {
    flex: 1 1 250px;
  }

  .bigger-logo {
    font-size: 3em;
  }
}

/* -------------------------
   💻 Desktop (≥1025px)
-------------------------- */
@media (min-width: 1025px) {
  .footer {
    padding: 3em 5em;
  }

  .bigger-logo {
    font-size: 4em;
  }
}


/* --- Mobile (≤767px) --- */
@media (max-width: 767px) {
  .footer {
    flex-direction: column;
    align-items: center;
    text-align: center;
    height: auto;
    padding: 2em 1.5em;
    gap: 2em; /* add space between sections */
  }

  .for-customers {
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .guide-container,
  .categories-container {
    width: 100%;
    align-items: center;
  }

  .social-media-container {
    width: 100%;
    text-align: center;
  }

  .social-icon-container {
    justify-content: center;
  }

  /* --- Footer 2 --- */
  .footer2-upper-section {
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 1.5em;
    padding: 2em 1em;
  }

  .footer2-right-section,
  .footer2-left-section {
    align-items: center;
  }

  .footer2-left-section {
    row-gap: 1em;
  }

  .bigger-logo {
    font-size: 2.5em;
  }

  .footer2-lower-section {
    font-size: 10px;
    height: auto;
    padding: 1.5em 0;
  }

  /* prevent overlap between footers */
  .footer2 {
    margin-top: 2em;
  }
}
