*{
  margin: 0;
  padding: 0;
  font-family: 'IRANSans', 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

body {
  direction: rtl;
  text-align: right;

  color: #3f4064;

  margin: 0;
  padding: 0;
  max-width: 100%;
  overflow-x: hidden;
  overflow-y: auto;     /* allow normal vertical scroll */
}

a{
  text-decoration: none;
  color: inherit;
}

.half-space{
  word-spacing: -0.30em;
}

.App{
  max-width: 100%;
  overflow-x: hidden;
}

.page-not-found{
  color: #3f4064ae;
  text-align: center;
  font-size: 4em;
}

html, body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
  width: 100%;
  box-sizing: border-box;
}
*, *::before, *::after {
  box-sizing: inherit;
}

.main-content{
  margin-top: 12em;
}



@media (max-width: 1024px) {
  .main-content{
    margin-top: 16.5em;
  }
}
@media (max-width: 768px) {
  .main-content{
    margin-top: 5em;
  }
}
@media (max-width: 480px) {
  .main-content{
    margin-top: 0;
  }
}


.not-found-page{
  text-align: center;
  font-size: 3em;
  font-weight: bold;
}