.payment-container {
  max-width: 400px;
  margin: 80px auto;
  background: #fff;
  padding: 30px;
  border-radius: 16px;
  box-shadow: 0 6px 16px rgba(0,0,0,0.1);
  text-align: center;
}

.payment-title {
  font-size: 1.6rem;
  margin-bottom: 20px;
  color: #222;
}

.payment-form {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.input-group {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.input-group label {
  font-size: 0.9rem;
  margin-bottom: 6px;
  color: #444;
}

.input-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 8px;
  font-size: 1rem;
  transition: border-color 0.2s ease;
}

.input-group input:focus {
  border-color: #007bff;
  outline: none;
}

.payment-row {
  display: flex;
  gap: 12px;
}

.pay-btn {
  margin-top: 10px;
  padding: 12px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 8px;
  transition: background 0.3s ease;
}

.pay-btn.processing {
  background-color: #6c757d;
  cursor: not-allowed;
}

.pay-btn:hover:not(.processing) {
  background-color: #0056b3;
}

.payment-success {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 12px;
  color: #28a745;
}

.success-icon {
  color: #28a745;
}

@media (max-width: 480px) {
  .payment-container {
    margin: 40px 20px;
    padding: 20px;
  }

  .payment-row {
    flex-direction: column;
  }
}
