.profile-page {
  min-height: 80vh;
  background-color: #fcfcfc;
  padding: 20px;
  display: flex;
  justify-content: center;
}

.profile-content {
  display: flex;
  max-width: 1200px;
  width: 100%;
  gap: 20px;
}

.profile-sidebar {
  flex: 1;
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.sidebar-link, .logout-btn {
  display: block;
  margin: 5px 0;
  color: #007bff;
  text-decoration: none;
  cursor: pointer;
}

.logout-btn {
  color: red;
  margin-top: auto;
}

.profile-info {
  flex: 3;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.profile-card {
  background: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.profile-header h2 {
  margin: 0 0 10px 0;
}

.profile-columns {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  height: 20em;
}

.profile-column {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.profile-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5em 1em;

  border: solid 1px #a1a3a853;
  border-radius: 0.25em;
}

.edit-btn {
  color: #007bff;
  cursor: pointer;
  background: none;
  border: none;
  font-size: 14px;
}

.popup-overlay {
  position: fixed;
  top: 2em;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 50;
}

.popup-container {
  background: white;
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.popup-input {
  width: 95%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.popup-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
}

.popup-save {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.popup-cancel {
  background-color: #ccc;
  color: black;
  border: none;
  padding: 8px 15px;
  border-radius: 5px;
  cursor: pointer;
}

.login-prompt {
  font-size: 18px;
  color: gray;
  text-align: center;
  margin-top: 50px;
}

.edit-icon{
  height: 2.5em;
  width: auto;
}

.dark-gray{
  color: #a1a3a8;
}

.profile-row > span{
  display: flex;
  flex-direction: column;
}
.profile-row > span > div{
  padding: 0.25em 0;
}
.profile-row.phone{
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.profile-row.phone > div{
  padding: 0.25em 0;
}

.popup-input[type=number]::-webkit-inner-spin-button,
.popup-input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.popup-input[type=number] {
  -moz-appearance: textfield;
}




/* ========== Responsive Styles ========== */

/* --- Tablets (max-width: 1024px) --- */
@media (min-width: 768px) and (max-width: 1024px) {
  .profile-content {
    flex-direction: column-reverse;
    align-items: center;
  }

  .profile-sidebar {
    width: 100%;
    max-width: 600px;
    text-align: center;
  }

  .profile-info {
    width: 100%;
    max-width: 600px;
  }

  .profile-columns {
    flex-direction: column;
    height: auto;
  }

  .profile-column {
    width: 100%;
  }
}

/* --- Mobile (max-width: 600px) --- */
@media (min-width: 481px) and (max-width: 767px) {
  .profile-content{
    flex-direction: column-reverse;
  }

  .profile-page {
    padding: 10px;
  }

  .profile-content {
    gap: 10px;
  }

  .profile-sidebar {
    padding: 15px;
    font-size: 0.9rem;
  }

  .sidebar-link,
  .logout-btn {
    font-size: 0.9rem;
    padding: 0.5em 0;
  }

  .profile-info {
    gap: 10px;
  }

  .profile-card {
    padding: 15px;
  }

  .profile-columns {
    flex-direction: column;
    gap: 10px;
  }

  .profile-row {
    flex-direction: column;
    align-items: flex-start;
    padding: 0.75em;
  }

  .profile-row > span > div {
    padding: 0.2em 0;
  }

  .edit-icon {
    height: 2em;
  }

  .popup-container {
    width: 90%;
    padding: 15px;
  }

  .popup-input {
    font-size: 0.9rem;
    padding: 8px;
  }

  .popup-buttons {
    flex-direction: column;
    gap: 8px;
  }

  .popup-save,
  .popup-cancel {
    width: 100%;
    font-size: 0.9rem;
    padding: 10px;
  }

  .login-prompt {
    font-size: 16px;
    margin-top: 30px;
  }
}

/* --- Extra Small (max-width: 400px) --- */
@media (max-width: 480px) {
  .profile-content{
    padding: 3em 0;
    flex-direction: column-reverse;
  }

  .profile-sidebar {
    font-size: 0.85rem;
    padding: 10px;
  }

  .profile-card {
    padding: 10px;
  }

  .profile-row {
    padding: 0.5em;
  }

  .popup-container {
    width: 95%;
  }
}
