.product-page-container {
  max-width: 100%;
  margin: 2rem auto;
  padding: 0 1rem;
}

.product-page {
  display: flex;
  flex-direction: row;
  gap: 4rem;
  align-items: flex-start;
  justify-content: space-between;
  object-fit: contain;
  border-radius: 10px;
  padding: 2em 0;
}

.product-image-section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  height: 100%;
}

.each-product-image {
  height: auto;
  width: auto;
  max-width: 314px;
}

.product-info-section {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  row-gap: 1rem;
}

.each-product-title {
  font-size: 1.5rem;
  font-weight: bold;
  color: #333;
}
.each-product-price-rating {
  display: flex;
  flex-direction: column; /* stack price + rating */
  align-items: center;
  justify-content: space-between;

  background: #ffffff;
  border: 1px solid #e0e0e0;
  border-radius: 12px;
  padding: 1em 3em;
  margin: 1em 0;
  min-height: 20em;
  min-width: 17em;

  transition: transform 0.2s ease, box-shadow 0.2s ease;

  position: sticky;
  top: 11em;            /* how far from the top before sticking */
  z-index: 100;     /* keeps it above other content */
  background: white; /* usually needed so it doesn’t overlap transparently */
}

.each-product-price {
  font-size: 1.5em;
  font-weight: bold;
  color: #ec2644; /* highlight color */
  margin-bottom: 0.5em;
}

.each-product-rating {
  font-size: 0.9em;
  color: #555;
}


.each-product-description h3 {
  font-size: 1.3rem;
  font-weight: 600;
  margin-bottom: 1rem;
}

.each-product-description p {
  font-size: 0.9rem;
  line-height: 1.5;
  color: #555;
}

.add-to-cart-btn {
  margin-top: 1rem;
  background-color: #ec2644;
  color: #fff;
  font-weight: bold;
  border: none;
  padding: 1.5em 0.5em;
  width: 100%;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.add-to-cart-btn:hover {
  background-color: #d41e38;
}
.add-to-cart-btn:active {
  opacity: 0.7;
}

/* --- Responsive --- */
@media screen and (max-width: 1024px) {
  .product-page {
    flex-direction: column;
    align-items: center;
  }

  .product-info-section {
    width: 100%;
  }
}

@media screen and (max-width: 480px) {
  .each-product-title {
    font-size: 1.5rem;
  }

  .product-price {
    font-size: 1.3rem;
  }

  .add-to-cart-btn {
    width: 100%;
    text-align: center;
  }
}


.quantity-container{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0.5em 1em;
  width: 100%;
  background-color: #f2f4fb;
  border-radius: 5px;
}
.quantity{
  align-self: center;
}

.add-button,
.reduce-button{
  background-color: #ffffff;
  border: none;
  border-radius: 5px;
  width: 3em;
  height: 3em;
}

.count-container{
  width: 100%;
  font-size: 0.8em;
}
.opinion-count{
  padding: 1em 0;
}

.is-favorite{
  height: 1.5em;
  width: auto;
  cursor: pointer;
}



/* --- Tablets --- */
@media (max-width: 1024px) {
  .product-page {
    flex-direction: column;
    align-items: center;
    gap: 2rem;
  }

  .each-product-price-rating {
    position: relative;
    top: 0;
    min-width: 100%;
  }

  .each-product-image {
    max-width: 280px;
  }

  .each-product-title {
    text-align: center;
  }

  .product-info-section {
    align-items: center;
    text-align: center;
  }
}

/* --- Mobile --- */
@media (max-width: 600px) {
  .product-page {
    gap: 1.5rem;
  }

  .each-product-title {
    font-size: 1.3rem;
  }

  .each-product-description h3 {
    font-size: 1.1rem;
  }

  .each-product-description p {
    font-size: 0.85rem;
  }

  .each-product-image {
    max-width: 200px;
  }

  .each-product-price-rating {
    padding: 1em;
    min-height: auto;
    width: 100%;
    gap: 1em;
  }

  .quantity-container {
    padding: 0.5em;
  }

  .add-button,
  .reduce-button {
    width: 2.5em;
    height: 2.5em;
  }

  .add-to-cart-btn {
    font-size: 0.9em;
    padding: 1em;
  }

  .product-image-section{
    flex-direction: column;
  }
}