.products-container {
  max-width: fit-content;
  display: flex;
  flex-wrap: wrap;
  /* 👈 allow items to move to next row */
  justify-content: center;
  margin: 0 auto;
  margin-bottom: 5em;
}

.product-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  row-gap: 1em;
  width: 17.4em;
  height: 27.4em;
  flex-wrap: wrap;
  background-color: #fff;
  border: none;
  border-right: solid 1px #00000012;
  border-bottom: solid 1px #00000012;
  cursor: pointer;
  box-sizing: border-box;
  outline: none;
}

.product-card:hover {
  box-shadow:
    0 6px 12px rgba(0, 0, 0, 0.1),
    0 12px 24px rgba(0, 0, 0, 0.08),
    0 18px 36px rgba(0, 0, 0, 0.05);
  /* چند لایه سایه */
  border-color: #fff;
  /* مرز محو میشه */
  z-index: 1;
}

.product-image {
  height: 50%;
  width: auto;
  max-width: 80%;
  padding: 1em 0;
}

.product-title {
  font-size: 0.95em;
  line-height: 1.4em;
  min-height: 2.8em;
  /* enough for two lines */
  max-height: 4.2em;
  /* prevents overflow on very long names */
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  /* limit to 3 lines */
  -webkit-box-orient: vertical;
  padding: 0 1em;
}


.rating-remaining {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin-top: auto;
  padding-top: 0.5em;
  font-size: 0.85em;
}

.remaining {
  font-size: 0.8em;
  color: #fd4949;
  font-weight: 500;
}

.rating {
  font-size: 0.8em;
  direction: ltr;
  display: flex;
  flex-direction: row;
  text-align: center;
  column-gap: 0.15em;
}

.count {
  font-size: 0.7em;
  align-self: flex-end;
  padding: 2px 0;
  color: #808080da;
}

.price {
  margin-top: auto;
  /* pushes it to the bottom */
  font-weight: bold;
  margin-right: auto;
  padding: 1em 0.5em;
  color: inherit;
}

.product-title,
.price,
.rating-remaining {
  direction: rtl;
}

/* =====================
   RESPONSIVENESS
   ===================== */

/* Large tablets and small desktops */
@media (max-width: 1200px) {
  .products-container {
    padding: 0 2em;
  }

  .product-image {
    max-width: 180px;
  }

  .product-title {
    font-size: 0.9em;
  }
}

/* Tablets */
@media (max-width: 768px) {
  .products-container {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
    padding: 0 1.5em;
  }

  .product-card {
    padding: 1em;
  }

  .product-image {
    max-width: 160px;
  }

  .price {
    font-size: 0.9em;
  }
}

/* Mobile phones */
@media (max-width: 480px) {
  .products-container {
    grid-template-columns: 1fr;
    margin-top: 3em;
    padding: 0 1em;
  }

  .product-card {
    padding: 1em 0.8em;
  }

  .product-title {
    font-size: 0.9em;
    -webkit-line-clamp: 2;
  }

  .product-image {
    max-width: 140px;
  }

  .rating-remaining {
    font-size: 0.75em;
  }

  .price {
    font-size: 0.85em;
  }
}