/* Base layout */
.contact-page {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 2rem;
  padding: 6em 3em;
  direction: rtl;
  font-family: Arial, sans-serif;
  box-sizing: border-box;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  margin-bottom: 5em;
  background-color: #fff;
}

.contact-logo-container {
  text-align: center;
}

/* Column sections */
.contact-info,
.map,
.contact-form {
  padding: 1.5rem;
  border-radius: 10px;
}

.map {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 65vh;
  padding-top: 0;
}

.contact-form {
  box-shadow: 4px 4px 6px rgba(0, 0, 0, 0.1);
  background: #fafafa;
}

/* Map styling */
.contact-page iframe {
  width: 100%;
  height: 100%;
  min-height: 300px;
  border: none;
  border-radius: 10px;
}

/* Info section layout */
.contact-city-container,
.contact-email-container,
.contact-phone-container,
.contact-time-container {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

.contact-info img {
  width: 24px;
  height: 24px;
}

.shop-city,
.shop-email,
.shop-phone,
.shop-time {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  gap: 0.5rem;
  font-size: 0.9em;
}

.contact-info p {
  margin: 0.5rem 0;
  font-weight: 600;
  font-size: 0.85em;
}

.horizontal-line {
  width: 100%;
  height: 1px;
  background: #ddd;
}

/* --- Form Styling --- */
.contact-form {
  display: flex;
  flex-direction: column;
  gap: 1.2rem;
}

.contact-form input,
.contact-form textarea {
  border: none;
  border-bottom: 2px solid #ccc;
  padding: 0.5rem;
  outline: none;
  font-size: 1rem;
  background-color: transparent;
  transition: border-color 0.3s;
}

.contact-form input:focus,
.contact-form textarea:focus {
  border-bottom-color: #007bff;
}

.phone-input {
  text-align: end;
}

.contact-form textarea {
  resize: vertical;
  min-height: 120px;
}

/* Submit button */
.submit-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  padding: 0.75rem;
  background-color: #007bff;
  color: white;
  font-weight: 600;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 3em;
}

.submit-button:hover {
  background-color: #0056b3;
}

.submit-button img {
  width: 20px;
  height: 20px;
}

/* ==============================
   RESPONSIVENESS
   ============================== */

/* Medium screens (large tablets / small laptops) */
@media (max-width: 1200px) {
  .contact-page {
    grid-template-columns: 1fr 1fr;
    padding: 4em 2em;
  }

  .map {
    height: 50vh;
  }

  .contact-info,
  .contact-form,
  .map {
    padding: 1rem;
  }
}

/* Tablets */
@media (max-width: 900px) {
  .contact-page {
    grid-template-columns: 1fr;
    gap: 2.5rem;
    padding: 3em 1.5em;
  }

  .contact-info,
  .map,
  .contact-form {
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.08);
  }

  .map {
    height: 50vh;
  }

  .contact-info p,
  .shop-city p {
    font-size: 0.9em;
  }

  .submit-button {
    margin-top: 2em;
  }
}

/* Phones */
@media (max-width: 600px) {
  .contact-page {
    padding: 2em 1em;
    gap: 1.5rem;
  }

  .contact-info,
  .contact-form,
  .map {
    padding: 1rem;
  }

  .shop-city,
  .shop-email,
  .shop-phone,
  .shop-time {
    gap: 0.3rem;
    font-size: 0.8em;
  }

  .contact-info img {
    width: 20px;
    height: 20px;
  }

  .submit-button {
    font-size: 0.9em;
    padding: 0.6rem;
  }

  .submit-button img {
    width: 16px;
    height: 16px;
  }

  .contact-form input,
  .contact-form textarea {
    font-size: 0.9em;
  }

  .map {
    height: 45vh;
  }
}
